.headerbuttonclose{height:fit-content;width:fit-content;top:0;left:0;background:url("../../images/filter.svg") no-repeat;font-size:20px;line-height:20px;font-weight:700;color:#192144;text-transform:uppercase;border:0;background-color:inherit;cursor:pointer;padding-left:45px;position:relative;font-family:inherit}@media(max-width: 767px){.headerbuttonclose{font-size:10px}}.headerbuttonclose div{padding-left:28px}@media(max-width: 1023px){.headerbuttonclose::after{display:block;content:"свернуть";text-transform:uppercase;position:absolute;width:100%;height:100%;opacity:1;background-color:#e2e5f2;left:0;background:url(../../images/icon-close-countries.svg) no-repeat #e2e5f2;background-position:right 0 top 50%;top:0;z-index:2}}@media(max-width: 1023px)and (max-width: 767px){.headerbuttonclose::after{display:none}}