.headerbutton{content:"Фильтрация по странам:";height:fit-content;width:fit-content;top:0;left:0;background:url("../../images/filter.svg") no-repeat;font-size:20px;line-height:20px;font-weight:700;color:#192144;text-transform:uppercase;border:0;background-color:rgba(0,0,0,0);cursor:pointer;padding-left:45px;position:relative;letter-spacing:-0.1px;font-family:inherit}@media(max-width: 1023px){.headerbutton{padding-left:2px;max-width:205px;margin-left:auto}}@media(max-width: 767px){.headerbutton{display:flex;flex-wrap:wrap;max-width:100%;width:100%;font-size:16px;line-height:18px;background-size:15px 10px;background-position:0 5px;text-align:left;padding-left:26px}.headerbutton::before{content:"";width:3px;height:3px;border-radius:50%;background-color:#192144;position:absolute;right:0;top:50%;box-shadow:-7px 0 0 #192144,-14px 0 0 #192144}}.headerbutton div{padding-left:28px}@media(max-width: 1023px){.headerbutton::after{display:flex;align-items:flex-start;content:"показать все";position:absolute;width:100%;height:100%;opacity:1;left:0;background:url(../../images/points.svg) no-repeat #edeff6;background-position:right 0 top 15%;top:0;z-index:2;text-transform:uppercase;font-family:inherit}}@media(max-width: 1023px)and (max-width: 767px){.headerbutton::after{display:none}}